<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process143"></a>

<p><br></p>
<p><span class="rvts17">主线：复仇女士号（</span><span class="rvts50">The Lady Vengeance</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:267,Y:498</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：亚历山大</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">启动战船，迎风起航</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1083.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在登上复仇女士号之后，第一件事就是遭遇了船无法启动的尴尬。根据马拉迪的描述，该船是由达利斯制造，并在之前是由亚历山大的军队控制。</span>
<br><span class="rvts21">现如今亚历山大已经在上一章节被我们打败，被囚禁在战船中层的监狱里。接下来的首要任务是寻找到控制船的方法。</span>
<br><span class="rvts21">由于该船地图区域极小，因此不难找到一些线索，最终可以了解船是由魔法歌曲控制。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1084.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">找到亚历山大被囚禁的位置，让牢头打开大门，并从他身上搜索出一块特殊的宝石。该宝石是进入行政舱的重要任务物品。</span>
<br><span class="rvts21">你可以说服亚历山大身边的跟班儿或直接打败她，来获取这块宝石。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1085.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">接着便是来到这处需要钥匙</span><span class="rvts20">+</span><span class="rvts21">密码的行政舱大门，经过一番周折的对话，最终获得大门密码为</span><span class="rvts20"></span><span class="rvts21">坚毅</span><span class="rvts20"></span><span class="rvts21">，打开大门便可以进入行政舱。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1086.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">行政舱内拥有很多的书籍和有用的物品。需要找到在亚历山大的那张华丽的床旁边的小柜子，里面有一本可以控制船启动的音乐谱。</span>
<br><span class="rvts21">另外在沙发左边，拥有一个密室通道，里面也有很多好的装备，但被两个斗篷刺客看守。</span>
<br><span class="rvts21">在沙发右边有一个被亚历山大软禁的犯人，叫做塔昆（</span><span class="rvts20">Tarquin</span><span class="rvts21">），可以在他这里买到技能书和一些额外的物品。可以选择杀了他或留他活口。</span>
<br><span class="rvts21">但是强烈建议留下他，之后章节该</span><span class="rvts20">NPC</span><span class="rvts21">将是一个非常重要的人物。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1087.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">当了解到船是由音乐控制，并找到谱子后，可以告诉马拉迪准备出发。无论选择是奴隶该船还是控制该船，都会把导致需要把船开走的结局。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1088.jpg" display="block" background-size="contain|cover"><span class="rvts20"></span>
<br><span class="rvts21">走到具有龙头的船头位置，操作它，复仇女神号迎风起航。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



